.Navbar {
    height: 70px;
    display: flex;
    align-content: center;
    background-color: #2b2b2b;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Navbar ul {
    list-style-type: none;
    margin: 0px 0px 0px 20px;
    padding: 0px;
    display: flex;
    vertical-align: middle;
}

.Navbar li {
    margin: 0px 20px 0px 0px;
    height: 70px;
    display: flex;
    text-align: center;
}

.Navbar span {
    font-size: 30px;
    font-weight: 700;
}

.logo {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.logo--red {
    color: red;
}
.logo--sec {
    color: white;
}

.Navbar button {
    border: none;
    background: inherit;
    color: white;
    opacity: 0.9;
    font-size: 20px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Navbar button[data-active=true] {
    color: red;
    opacity: 1;
}

.Navbar button:hover {
    opacity: 1;
}

.Navbar button:active {
    color: rgb(255, 200, 200);
}